<template>
  <div class="signIn">
    <div class="login-img">
      <!--      <img src="../../../assets/registerIcon/login_bg.png"/>-->
    </div>
    <div class="upData-input phone-error">
      <van-cell-group>
        <!-- 账号 -->
        <van-field
          v-model="account"
          ref="account"
          v-if="PasswordShow"
          :placeholder="$t('EnterAccount')"
          @input="inputAccount"
          :error-message="accountError"
          :disabled="stopAccount"
        >
        </van-field>
        <!-- 密码 -->
        <van-field
          :disabled="stopPassword"
          ref="password"
          type="password"
          class="forget-password"
          v-if="PasswordShow"
          v-model="Password"
          :placeholder="$t('Password')"
          :error-message="inputPasswordError"
          @input="inputPassword"
        >
          <van-button slot="button" @click="ForgetPassword"
            >忘记密码</van-button
          >
        </van-field>

        <!-- 手机号 -->
        <van-field
          v-model="Phone"
          type="number"
          placeholder="请输入手机号"
          v-if="codeShow"
          :error-message="inputPhoneError"
          @input="inputPhone"
        >
        </van-field>
        <van-field
          type="number"
          v-model="code"
          v-if="codeShow"
          placeholder="请输入短信验证码"
          :error-message="inputCodeError"
          @input="inputCode"
        >
          <van-button
            slot="button"
            class="code"
            @click="getyzcode"
            :disabled="attcode"
            v-if="showbtn"
            >获取验证码</van-button
          >
          <van-button slot="button" class="code" v-else>{{
            code_ts
          }}</van-button>
        </van-field>
        <!-- 验证码 -->
        <!-- <van-field
          :disabled="disCode"
          class="forget-password1"
          ref="code"
          v-if="codeShow"
          v-model="code"
          :placeholder="placeCode"
          :error-message="inputCodeError"
          @input="inputCode"
          @click="clickCode"
        >
          <van-button
            slot="button"
            class="code"
            @click.stop="getyzcode"
            :disabled="attcode"
            v-if="showbtn"
            >{{ $t("getCode") }}</van-button
          >
          <van-button slot="button" class="code" v-else>{{
            code_ts
          }}</van-button>
        </van-field> -->
      </van-cell-group>
    </div>
    <div class="upBankCard-statement pass" v-if="!loginModeOff">
      <van-checkbox
        shape="square"
        v-model="RememberPW"
        checked-color="#1988FF"
        >{{ $t("RememberPass") }}</van-checkbox
      >
    </div>
    <div class="upBankCard-statement pass" v-else></div>
    <!-- <p class="changelogin" >
      <span @click="setLoginMode">切换登录方式</span>
    </p> -->
    <!--    <div class="upBankCard-but" >登录</div>-->
    <van-button
      class="upBankCard-but"
      :disabled="!statement"
      @click="handdleMsg"
      >{{ $t("SignIn") }}</van-button
    >
    <div
      class="input-switch"
      :style="{
        'justify-content': $i18n.locale !== 'zh' ? 'center' : 'space-between',
      }"
    >
      <div @click="linkOpenAccount">
        {{ $t("logonAsk")
        }}<span style="color: #1988ff">{{ $t("register") }}</span>
      </div>
      <div
        @click="setLanguage"
        style="color: #1988ff"
        :style="{ 'margin-top': $i18n.locale !== 'zh' ? '6px' : '' }"
      >
        {{ $t("language") }}
      </div>
    </div>
    <div
      class="upBankCard-statement"
      :class="$i18n.locale !== 'zh' ? 'upBankCard-statement-en' : ''"
    >
      <div class="upBankCard-statement-box">
        <van-checkbox shape="square" v-model="statement" checked-color="#1988FF"
          >{{ $t("Agreement")
          }}<span style="color: #1988ff" @click="linkUserAgre">{{
            $t("AgreementContent")
          }}</span></van-checkbox
        >
      </div>
    </div>
    <!-- ------------------------------zlq----------------------------- -->
    <van-dialog
      v-model="showLanguage"
      confirm-button-color="#1988FF"
      :title="$t('language')"
      :confirm-button-text="$t('Confirm')"
      className="setLanguage"
      @confirm="confirmLanguage"
    >
      <!-- ------------------------------zlq----------------------------- -->
      <van-radio-group v-model="languageRadio">
        <van-radio name="zh">简体中文</van-radio>
        <van-radio name="en">English</van-radio>
        <van-radio name="ja">日本語</van-radio>
      </van-radio-group>
    </van-dialog>

    <router-view @setAgree="getAgree"></router-view>
  </div>
</template>

<script>
import EP from "../../../utils/encryption";
import global from "../../../global";
import { Toast } from "vant";
import config from "../../../../package";
export default {
  name: "login",
  data() {
    return {
      stopAccount: false,
      stopPassword: false,
      // 账号/手机号
      account: "",
      accountError: "",
      // 密码
      Password: "",
      inputPasswordError: "",
      // 短信验证码
      code: "",
      inputCodeError: "",
      accountTips: "",
      // 短信验证码登录-密码输入框显示隐藏
      PasswordShow: true,
      // 短信验证码登录-验证码输入框显示隐藏
      codeShow: false,
      // 登录方式切换
      loginMode: "验证码登录",
      loginModeOff: false,
      // 是否记住密码
      RememberPW: false,
      // 是否同意声明
      statement: true,
      attcode: true, // 点击获取验证码按钮判断
      showbtn: true, // 展示获取验证码或倒计时按钮判断
      code_ts: "获取验证码", // 倒计时提示文字
      sec: 60, // 倒计时秒数
      // 定义websocket
      websock: null,
      // 存储验证码返回字段
      keying: "",
      // 已注册，直接登录
      signIn: false,
      // 盘口类型 盘口URL
      discUrl: "",
      heartCheck: {},
      webscoketFlag: true,
      sendTime: null,
      disCode: true,
      placeCode: "请点击获取短信验证码",
      // 设备信息
      getInfo: "",
      uuid: "",
      loginType: 0,
      // 语言设置
      showLanguage: false,
      languageRadio: "zh",

      inputPhoneError: "",
      inputPhoneErrorOff: true,

      // 账号/手机号
      Phone: "",
    };
  },
  watch: {
    account: function (value) {
      let reg = /\b1[3456789]\d{9}\b/g;
      let val = reg.test(value);
      if (val) {
        this.attcode = false;
      } else {
        this.attcode = true;
      }
    },
    Phone: function (value) {
      let reg = /\b1[3456789]\d{9}\b/g;
      let val = reg.test(value);
      if (val) {
        this.attcode = false;
      } else {
        this.attcode = true;
      }
    },
  },
  beforeDestroy() {
    if (this.sandTime) {
      clearInterval(this.sandTime);
    }
  },
  beforeRouteEnter(to, from, next) {
    console.log(to);
    console.log(from);
    next((vm) => {
      if (
        from.name === "home" ||
        from.name === "market" ||
        from.name === "trade" ||
        from.name === "my"
      ) {
        vm.Logout();
      }
    });
  },
  created() {
    let that = this;
    localStorage.setItem("back", "signIn");
    this.languageRadio = localStorage.i18n;
    let Flag = localStorage.getItem("Flag");
    if (Flag === "isLogin" && localStorage.account !== undefined) {
      location.reload();
      localStorage.removeItem("Flag");
      localStorage.removeItem("account");
    }
    if (localStorage.getItem("error")) {
      location.reload();
      localStorage.removeItem("error");
    }
    this.generateUUID();
    // document.addEventListener('plusready',function () {
    //   that.getInfo = plus.os.name+config.version
    //   localStorage.setItem('getInfo',that.getInfo)
    //   that.uuid = plus.device.uuid
    //   localStorage.setItem('uuid',that.uuid)
    // },false);
    localStorage.setItem("abnormal", "true");

    this.discUrl = localStorage.getItem("discUrl");
    let remember = JSON.parse(localStorage.getItem("remember"));
    console.log(remember);
    if (remember.environment === "reality") {
      this.account = remember.LoginAccount;
      if (remember.RememberPW === true) {
        this.RememberPW = true;
        this.Password = EP.decrypt(remember.pass, "");
      }
    } else {
      this.account = this.$route.params.phone;
    }
    if (that.global.ws && that.global.ws.readyState !== 1) {
      that.$emit("reLogin", "121212");
    }
  },
  // mounted(){
  //   if (window.history && window.history.pushState) {
  //     history.pushState(null, null, document.URL);
  //     window.addEventListener('popstate', this.fun, false);//false阻止默认事件
  //   }
  // },
  // destroyed(){
  //   window.removeEventListener('popstate', this.fun, false);//false阻止默认事件
  //   this.$destroy('login')
  // },
  methods: {
    inputPhone() {
      let value = this.Phone;
      let reg = /[a-zA-Z0-9_]$/g;
      let val = reg.test(value);
      if (val) {
        this.inputPhoneError = "";
        this.inputPhoneErrorOff = true;
      } else {
        this.inputPhoneError = this.$t("accountError");
        this.inputPhoneErrorOff = false;
      }
    },
    getUserIP(onNewIP) {
      let MyPeerConnection =
        window.RTCPeerConnection ||
        window.mozRTCPeerConnection ||
        window.webkitRTCPeerConnection;
      let pc = new MyPeerConnection({
        iceServers: [],
      });
      let noop = () => {};
      let localIPs = {};
      let ipRegex = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/g;
      let iterateIP = (ip) => {
        if (!localIPs[ip]) onNewIP(ip);
        localIPs[ip] = true;
      };
      pc.createDataChannel("");
      pc.createOffer()
        .then((sdp) => {
          sdp.sdp.split("\n").forEach(function (line) {
            if (line.indexOf("candidate") < 0) return;
            line.match(ipRegex).forEach(iterateIP);
          });
          pc.setLocalDescription(sdp, noop, noop);
        })
        .catch((reason) => {});
      pc.onicecandidate = (ice) => {
        if (
          !ice ||
          !ice.candidate ||
          !ice.candidate.candidate ||
          !ice.candidate.candidate.match(ipRegex)
        )
          return;
        ice.candidate.candidate.match(ipRegex).forEach(iterateIP);
      };
    },
    generateUUID() {
      let d = new Date().getTime();
      this.uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
          let r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
      localStorage.setItem("uuid", this.uuid);
    },
    getAgree(data) {
      this.statement = data;
    },
    linkUserAgre() {
      this.$router.push({
        name: "userAgre",
      });
    },
    clickCode() {
      if (this.showbtn && this.code === "") {
        Toast({
          duration: 1000,
          position: "bottom",
          message: "请先获取手机验证码",
        });
      }
    },
    fun() {
      console.log("监听到了");
    },
    // 验证账号
    inputAccount() {
      let value = this.account;
      let reg = /[a-zA-Z0-9_]$/g;
      let val = reg.test(value);
      if (val) {
        this.accountError = "";
      } else {
        this.accountError = this.$t("accountError");
      }
    },
    inputCode() {
      let value = this.code;
      let reg = /^\d{5}$/g;
      let val = reg.test(value);
      console.log(val);
      if (val) {
        this.inputCodeError = "";
      } else {
        this.inputCodeError = "验证码输入有误，请重新确认！";
      }
    },
    inputPassword() {
      let value = this.Password;
      let reg = /^ +| +$/g;
      let val = reg.test(value);
      if (!val) {
        this.inputPasswordError = "";
      } else {
        this.inputPasswordError = this.$t("inputPasswordError");
      }
    },
    // 登录方式切换
    setLoginMode() {
      if (this.loginModeOff) {
        this.loginMode = "验证码登录";
        this.accountTips = this.$t("EnterAccount");
        this.PasswordShow = true;
        this.codeShow = false;
      } else {
        this.accountTips = this.$t("EnterPhone");
        this.loginMode = "密码登录";
        this.PasswordShow = false;
        this.codeShow = true;
      }
      this.loginModeOff = !this.loginModeOff;
    },
    // 跳转到注册
    linkOpenAccount() {
      this.$router.push({
        name: "openAccount",
      });
    },
    // 忘记密码
    ForgetPassword() {
      let paramsPhone = this.account;
      let reg = /\b1[3456789]\d{9}\b/g;
      let val = reg.test(paramsPhone);
      console.log(val);
      if (val === true) {
        this.$router.push({
          name: `changePassword`,
          params: {
            phone: paramsPhone,
          },
        });
      } else {
        this.$router.push({
          name: `changePassword`,
        });
      }
    },
    // 连接
    // 获取验证码倒计时
    getyzcode() {
      let that = this;
      this.disCode = false;
      this.placeCode = "请输入短信验证码";
      // 短信发送失败，提示手机号已注册
      this.$axios
        .get("http://" + this.discUrl + ":7777/api/user/sms/" + this.Phone)
        .then((res) => {
          console.log("验证码", res);
          if (res.data.code === 0 && res.data.object != null) {
            // that.$refs.code.focus();
            Toast({
              duration: 1000,
              position: "bottom",
              message: "验证码发送成功",
            });
            this.keying = res.data.object.keying;
            console.log("this.keying :>> ", this.keying);
            let timer = setInterval(() => {
              this.sec = this.sec - 1;
              this.code_ts = this.sec + "S后重试";
              this.showbtn = false;
              if (this.sec === 0) {
                clearInterval(timer);
                this.sec = 60;
                this.code_ts = this.sec + "S后重试";
                this.showbtn = true;
              }
            }, 1000);
          } else {
            Toast({
              duration: 1000,
              position: "bottom",
              message: "验证码发送失败",
            });
          }
        });
    },

    handdleMsg(msg) {
      let that = this;
      let reg = /\b1[3456789]\d{9}\b/g;
      let val = reg.test(that.account);
      this.loginType = val ? 1 : 0;
      console.log(that.global.ws, that.global.ws.readyState);
      // if( localStorage.ip !== '8.210.83.31'){
      if (that.global.ws && that.global.ws.readyState === 1) {
        that.global.ws.onopen = that.onopen();
      } else {
        this.$emit("reLogin", "121212");

        // -------------------------zlq----------------------------
        // 用户登录失败时提示
        this.$toast({
          message: "登录失败,请检查当前网络",
          duration: 2000,
        });
        // -------------------------zlq----------------------------
      }
      // }else {
      //   Toast({
      //     position:'bottom',
      //     message: this.$t('loginMsg2'),
      //     duration:2000
      //   })
      // }

      that.global.ws.onmessage = function (res) {
        that.websocketonmessage(res);
      };
    },

    websocketonmessage(e) {
      // 数据接收
      this.redata = JSON.parse(e.data);
      console.log(this.redata, "登录页");
      let _this = this;
      if (this.redata.action === "mutipointlogin") {
        localStorage.setItem("WhetherReconnect", true);
        Toast({
          message: _this.$t("jixMessage"),
          position: "bottom",
          duration: 2000,
          onOpened() {
            localStorage.jix = "jix";
            console.log("退出");
            _this.Logout();
          },
        });
      }
      if (this.redata.action === "login") {
        if (this.redata.data.code === 0) {
          console.log("_this.re :>> ", _this.redata);
          localStorage.removeItem("abnormal");
          let bankAccount = EP.encrypt(
            _this.redata.data.object.bankAccount,
            ""
          );
          let idcard = EP.encrypt(_this.redata.data.object.idcard, "");
          localStorage.setItem("Flag", "isLogin");
          localStorage.setItem("name", _this.redata.data.object.name);
          localStorage.setItem("phone", _this.redata.data.object.phone);
          localStorage.setItem("account", _this.redata.data.object.account);
          localStorage.setItem("idcard", idcard);
          localStorage.setItem(
            "idcardtype",
            _this.redata.data.object.idcardtype
          );
          localStorage.setItem("bank", _this.redata.data.object.bank);
          localStorage.setItem("bankAccount", bankAccount);
          localStorage.setItem("uid", _this.redata.data.object.id);
          localStorage.setItem("status", _this.redata.data.object.status);
          localStorage.setItem("token", _this.redata.data.object.token);
          localStorage.setItem(
            "isDrawPass",
            _this.redata.data.object.isDrawPass
          );
          let discount = EP.encrypt(_this.redata.data.object.discount, "");
          localStorage.setItem("discount", discount);

          if (!localStorage.remember) {
            localStorage.removeItem("Logout");
            localStorage.removeItem("error");
            localStorage.removeItem("jix");
            this.$router.replace({
              name: "home",
              params: {
                showRisk: "1",
              },
            });
          } else {
            localStorage.removeItem("Logout");
            localStorage.removeItem("error");
            localStorage.removeItem("jix");
            this.$router.replace({
              name: "home",
              params: {
                status: this.redata.data.object.status,
              },
            });
          }

          if (this.loginModeOff) {
            console.log(this.loginModeOff);
            localStorage.setItem("LoginAccount", this.account);
            localStorage.setItem("LoginCode", this.code);
            localStorage.setItem("LoginKeying", this.keying);
            localStorage.setItem("LoginMode", "codeLogin");
            let remember = {};
            remember.LoginAccount = this.account;
            remember.environment = localStorage.getItem("environment");
            localStorage.setItem("remember", JSON.stringify(remember));
          } else {
            let EPPassword = EP.encrypt(this.Password, "");
            localStorage.setItem("LoginAccount", this.account);
            localStorage.setItem("LoginPassword", EPPassword);
            localStorage.setItem("LoginMode", "passLogin");
            let remember = {};
            remember.LoginAccount = this.account;
            remember.RememberPW = this.RememberPW;
            remember.pass = EPPassword;
            remember.environment = localStorage.getItem("environment");
            localStorage.setItem("remember", JSON.stringify(remember));
          }
        } else {
          let msg = "";
          let data = _this.redata.data.msg;
          if (data === "用户已登录") {
            msg = this.$t("loginMsg3");
          } else if (data === "用户不存在") {
            msg = this.$t("loginMsg1");
          } else if (data === "账号或密码错误！") {
            msg = this.$t("loginMsg4");
          } else if (data === "验证码错误") {
            msg = this.$t("loginMsg5");
          } else {
            msg = this.$t("loginMsg2");
          }
          Toast({
            position: "bottom",
            message: msg,
            duration: 2000,
          });
        }
      }
    },
    onopen() {
      // websocket连接后发送数据(send发送)
      let actions;
      let that = this;
      if (this.loginModeOff) {
        if (this.code === "") {
          Toast({
            message: "验证码不能为空",
            duration: 1000,
            position: "bottom",
          });
        } else {
          console.log(this.loginModeOff);
          actions = {
            action: "login",
            reqID: 8,
            data: {
              loginType: 2,
              account: "",
              password: "",
              phone: this.Phone,
              loginMac: localStorage.uuid,
              client: "Mobile",
              version: localStorage.getInfo,
              keying: this.keying,
              code: this.code,
              reconnection: false,
            },
          };
          console.log("actions :>> ", actions);
        }
      } else {
        if (this.loginType === 0) {
          actions = {
            action: "login",
            reqID: 7,
            data: {
              loginType: 0,
              phone: "",
              account: this.account,
              password: this.Password,
              loginMac: localStorage.uuid,
              client: "Mobile",
              version: localStorage.getInfo,
              keying: "",
              code: "",
              reconnection: false,
            },
          };
        }
        if (this.loginType === 1) {
          actions = {
            action: "login",
            reqID: 7,
            data: {
              loginType: 1,
              phone: this.account,
              account: "",
              password: this.Password,
              loginMac: localStorage.uuid,
              client: "Mobile",
              version: localStorage.getInfo,
              keying: "",
              code: "",
              reconnection: false,
            },
          };
        }
      }
      console.log("发送");
      that.global.ws.send(JSON.stringify(actions));
    },
    websocketonerror() {
      // 连接建立失败重连
      this.getLogin();
    },
    // 退出登录
    linkLogout() {
      let that = this;
      if (that.global.ws && that.global.ws.readyState === 1) {
        that.global.ws.onopen = that.open2();
      }
      that.global.ws.onmessage = function (res) {
        that.message2(res);
      };
    },
    // 获取详细订单
    open2() {
      // websocket连接后发 送数据(send发送)
      let actions;
      let that = this;
      actions = {
        action: "logout",
        reqID: 99,
        data: {},
      };
      that.global.ws.send(JSON.stringify(actions));
    },
    // 接收详细订单
    message2(e) {
      // 数据接收
      let that = this;
      this.redata = JSON.parse(e.data);
      console.log(this.redata);
      if (this.redata.action === "logout" && this.redata.data.code === 0) {
        this.$store.commit("userStatus", "false");
        localStorage.setItem("discUrl", "121.43.137.150");
        localStorage.removeItem("Flag");
        localStorage.removeItem("name");
        localStorage.removeItem("account");
        localStorage.removeItem("uid");
        localStorage.removeItem("status");
        localStorage.removeItem("phone");
        localStorage.removeItem("disc");
        localStorage.removeItem("idcard");
        localStorage.removeItem("idcardtype");
        localStorage.removeItem("bank");
        localStorage.removeItem("bankAccount");
        localStorage.removeItem("isDrawPass");
        localStorage.removeItem("token");
        localStorage.removeItem("upTitle");
        localStorage.removeItem("positionid");
        localStorage.removeItem("LoginCode");
        localStorage.removeItem("LoginKeying");
        localStorage.removeItem("LoginPassword");
        localStorage.removeItem("LoginMode");
        that.$router.replace("/");
        that.global.ws.close();
      }
    },

    // 打开语言切换
    setLanguage() {
      this.showLanguage = true;
    },
    confirmLanguage() {
      console.log(this.languageRadio);
      // if(command === 'zh'){
      //   localStorage.setItem('i18n','zh')
      //   this.$i18n.locale = 'zh'
      //   this.language = '简体中文'
      // }else if((command === 'en')) {
      //   localStorage.setItem('i18n','en')
      //   this.$i18n.locale = 'en'
      //   this.language = 'English'
      // }else if((command === 'ja')) {
      //   localStorage.setItem('i18n','ja')
      //   this.$i18n.locale = 'ja'
      //   this.language = '日本語'
      // }
      if (this.languageRadio === "zh") {
        localStorage.setItem("i18n", "zh");
        this.$i18n.locale = "zh";
      } else if (this.languageRadio === "en") {
        localStorage.setItem("i18n", "en");
        this.$i18n.locale = "en";
      } else if (this.languageRadio === "ja") {
        localStorage.setItem("i18n", "ja");
        this.$i18n.locale = "ja";
      }
    },
  },
};
</script>

<style scoped>
.changelogin{
  font-size: 14px;
  padding: 0px 20px 10px;
  text-align: right;
}
</style>



